<template>
  <div>
    <!-- Overlay -->
    <div
      @click="$emit('close-dialog')"
      class="absolute inset-0 bg-black z-9000 opacity-25"
    ></div>

    <!-- Input Form -->
    <div class="absolute inset-0 z-9010 flex justify-center items-center">
      <div class="w-full mx-4 md:w-2/3 md:mx-0 p-4 grid grid-cols-4 gap-6 bg-tan rounded-lg">

        <!-- Buttons -->
        <div class="col-span-1 flex flex-col justify-between">
          <h2 v-if="!attrProps.id" class="mb-4 text-lg text-gray-700">Add Attraction</h2>
          <h2 v-else class="mb-4 text-lg text-700">Edit Attraction</h2>
          <div class="flex flex-col">
            <button
              v-if="!attrProps.id"
              @click="saveAttraction"
              class="px-4 py-1 mb-4 text-white bg-blue-600 hover:bg-blue-700 uppercase tracking-wider rounded "
            >Submit</button>
            <button
              v-else
              @click="updateAttraction"
              class="px-4 py-1 mb-4 text-white bg-blue-600 hover:bg-blue-700 uppercase tracking-wider rounded "
            >Submit</button>
            <button
              @click="$emit('close-dialog')"
              class="px-4 py-1 text-gray-700 font-bold border-2 border-gray-700 hover:shadow-lg uppercase tracking-wider rounded "
            >Cancel</button>
          </div>
        </div>

        <!-- Input Form -->
        <div class="col-span-3">
          <label class="block mb-4">
            <span class="text-gray-700">Name</span>
            <input
              v-model.trim="$v.name.$model"
              type="text"
              class="form-input mt-1 block w-full"
              placeholder="Attraction"
            >
            <div v-if="$v.name.$dirty">
              <div class="ml-3 text-red-700 text-xs italic" v-if="!$v.name.required">Name is required</div>
              <div class="ml-3 text-red-700 text-xs italic" v-if="!$v.name.maxLength">Name must have at most {{ $v.name.$params.maxLength.max }} letters.</div>
            </div>
          </label>
          <div class="flex justify-between">
            <label class="block mb-4">
              <span class="text-gray-700">Lattitude</span>
              <input
                v-model.trim="$v.lat.$model"
                v-model="lat"
                type="text"
                class="form-input mt-1 block w-full"
                placeholder="Attraction"
              >
              <div v-if="$v.lat.$dirty">
                <div class="ml-3 text-red-700 text-xs italic" v-if="!$v.lat.required">Latitude is required</div>
                <div class="ml-3 text-red-700 text-xs italic" v-if="!$v.lat.decimal">Latitude must be a decimal value</div>
              </div>
            </label>
            <label class="block mb-4">
              <span class="text-gray-700">Longitude</span>
              <input
                v-model.trim="$v.lng.$model"
                type="text"
                class="form-input mt-1 block w-full"
                placeholder="Attraction"
              >
              <div v-if="$v.lng.$dirty">
                <div class="ml-3 text-red-700 text-xs italic" v-if="!$v.lng.required">Longitude is required</div>
                <div class="ml-3 text-red-700 text-xs italic" v-if="!$v.lng.decimal">Longitude must be a decimal value</div>
              </div>
            </label>
          </div>
          <label class="block mb-4">
            <span class="text-gray-700">Category</span>
            <select v-model="$v.category.$model" class="form-select block w-full mt-1">
              <option disabled value="">Nothing selected</option>
              <option value="Park">Park</option>
              <option value="Museum">Museum</option>
              <option value="Place">Place</option>
              <option value="Neighborhood">Neighborhood</option>
              <option value="Pueblo Magico">Pueblo Magico</option>
            </select>
            <div v-if="$v.category.$dirty">
              <div class="ml-3 text-red-700 text-xs italic" v-if="!$v.category.required">Category is required</div>
            </div>
          </label>
          <label class="block mb-4">
            <span class="text-gray-700">Wikipedia Link</span>
            <input
              v-model="$v.web.$model"
              type="text"
              class="form-input mt-1 block w-full"
              placeholder="https://..."
            >
            <div v-if="$v.web.$dirty">
              <div class="ml-3 text-red-700 text-xs italic" v-if="!$v.web.required">Link is required.</div>
              <div class="ml-3 text-red-700 text-xs italic" v-if="!$v.web.url">Wikipedia Link must be a valid url.</div>
            </div>
          </label>
          <div class="grid grid-cols-5 gap-2">
            <div class="col-span-2">
              <div class="relative bg-red-400 pb-3/5">
                <!-- Image Preview -->
                <img
                  :src="imgPreview"
                  alt="Attraction Image for Upload"
                  @click="$refs.attrImg.click()"
                  class="absolute h-full w-full object-cover cursor-pointer"
                >
                <!-- Hidden file input -->
                <input
                  type="file"
                  accept="image/*"
                  class="hidden"
                  @change="onImgSelected"
                  ref="attrImg"
                >
              </div>
            </div>
            <div class="col-span-3">
              <label class="block">
                <span class="text-gray-700">Upload Image as</span>
                <input
                  v-model="$v.image.$model"
                  type="text"
                  class="form-input mt-1 block w-full"
                  placeholder="Image name without its extension"
                >
                <div v-if="$v.category.$dirty">
                  <div class="ml-3 text-red-700 text-xs italic" v-if="!$v.image.required">Image Upload Name is required.</div>
                  <div class="ml-3 text-red-700 text-xs italic" v-if="!$v.image.maxLength">Image Upload Name must have at most {{ $v.image.$params.maxLength.max }} letters.</div>
                </div>
              </label>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// Imports
import mainService from '@/services/mainService'
import { required, decimal, url, maxLength } from 'vuelidate/lib/validators'

// Export component instance
export default {
  name: "NewAttractionDialog",

  props: {
    attrGeom: {
      type: Object,
      required: true
    },
    attrProps: {
      type: Object,
      default: function () {
        return {
          category: ''
        }
      }
    }
  },

  created () {
    if (!this.attrProps.image) {
      this.imgPreview = 'http://via.placeholder.com/400x120.png?text=Click+to+select+image'
    } else {
      this.imgPreview = 'http://localhost:3000/img/' + this.attrProps.image
    }
  },

  data () {
    return {
      name            : this.attrProps.name,
      category        : this.attrProps.category,
      web             : this.attrProps.web,
      image           : this.attrProps.image,
      lat             : Math.round( 100000 * this.attrGeom.lat) / 100000,
      lng             : Math.round( 100000 * this.attrGeom.lng) / 100000,
      imgPreview      : null,
      uploadFile      : null
    }
  },

  validations : {
    name     : { required, maxLength: maxLength(50) },
    category : { required },
    web      : { required, url },
    image    : { required, maxLength: maxLength(20) },
    lat      : { required, decimal },
    lng      : { required, decimal }
  },

  methods: {
    async onImgSelected(event) {
      // extract selected image file from change event
      this.uploadFile = event.target.files[0]
      console.log(event.target.files[0])
      try {
        // Preview image file selected by user
        const result = await this.previewImage(this.uploadFile)
        this.imgPreview = result.success.preview
      } catch (error) {
        // Error while previewing the map selected
        alert (error)
      }
    },

    previewImage(imageFile) {
      return new Promise ((resolve, reject) => {
        // initialize File Reader
        let reader = new FileReader();
        // set handler for load event
        reader.onload = () => {
          resolve ({
            success : {
              preview : reader.result
            }
          });
        };
        // set handler for error event
        reader.onerror = (err) => {
          console.log('Reader Error', err)
          reject ("Cannot preview image!")
        };
        // verify file selected before engaging reader
        if (imageFile) {
          // verify file is image before engaging reader
          if (/\.(jpe?g|png|gif)$/i.test(imageFile.name)) {
            // read file and fire load event upon completion
            reader.readAsDataURL (imageFile);
          } else {
            // error: file is not an image
            reject ( "Please select only image files with extension jpeg, jpg, png or gif");
          }
        }
      })
    },

    async uploadImage() {
      const dotIndex = this.image.lastIndexOf('.')
      let imageWithoutExt
      if (/\.(jpe?g|png|gif)$/i.test(this.image)) {
        imageWithoutExt = this.image.substring(0, dotIndex)
      } else {
        imageWithoutExt = this.image
      }
      const fd = new FormData()
      // add meta data to req.body
      fd.append('dbFilename', imageWithoutExt);
      // add the upload file to req.file
      fd.append('image', this.uploadFile, this.uploadFile.name)

      const response = await mainService.uploadImage(fd);
      console.log(response)
      return response.data
    },

    async updateAttraction () {
      this.$v.$touch()
      if (!this.$v.$invalid) {
        try {
          const response = (await mainService.updateAttraction({
            id       : this.attrProps.id,
            name     : this.name,
            category : this.category,
            image    : this.image,
            web      : this.web,
            lat      : +this.lat,
            lng      : +this.lng
          })).data

          if (response.success) {
            this.$emit ('update-feature', {
              type: "Feature",
              properties: {
                id       : this.attrProps.id,
                name     : this.name,
                category : this.category,
                image    : this.image,
                web      : this.web,
              },
              geometry: {
                type: "Point",
                coordinates: [this.lng, this.lat]
              }
            })
          }
        } catch (error) {
          alert(error.message)
        }
      }
    },

    async saveAttraction () {
      this.$v.$touch()
      if (!this.$v.$invalid) {
        try {
          const uploadResp = await this.uploadImage()
          if (uploadResp.success) {
            this.image = uploadResp.success
          } else {
            throw new Error('Image Upload unsuccessful. Attraction was not saved!')
          }
          const response = (await mainService.saveAttraction({
            name     : this.name,
            category : this.category,
            image    : this.image,
            web      : this.web,
            lat      : +this.lat,
            lng      : +this.lng
          })).data

          if (response.success) {
            this.$emit ('add-feature', {
              type: "Feature",
              properties: {
                id       : response.success.id,
                name     : this.name,
                category : this.category,
                image    : this.image,
                web      : this.web,
              },
              geometry: {
                type: "Point",
                coordinates: [this.lng, this.lat]
              }
            })
          }
        } catch (error) {
          alert(error.message)
        }
      }
    }
  }

}
</script>
