<template>
  <div class="container mx-auto relative">

    <!-- Header -->
    <div class="bg-orange-400 text-center">
      <h1 class="py-4 text-4xl">Mexico City</h1>
    </div>
    <div class="grid grid-cols-4">
      <!-- Side Panel -->
      <div class="col-span-4 md:col-span-1 p-2 bg-beige">
        <h2 class="text-2xl text-center">Attractions</h2>
        <div class="flex justify-between items-center">
          <label class="w-1/3 text-gray-700">Filter</label>
          <select
            v-model="filterCategory"
            class="form-select w-2/3"
          >
            <option value="">Nothing selected</option>
            <option value="Park">Park</option>
            <option value="Museum">Museum</option>
            <option value="Place">Place</option>
            <option value="Neighborhood">Neighborhood</option>
            <option value="Pueblo Magico">Pueblo Magico</option>
          </select>
        </div>
        <button
          v-for="attraction in filteredAttractions"
          :key="attraction.properties.id"
          class="w-full my-1 py-1 bg-blue-600 hover:bg-blue-500 hover:shadow-lg focus:outline-none focus:shadow-outline rounded uppercase"
          @click="zoomToLocation(attraction.geometry.coordinates)"
        >{{ attraction.properties.name }}</button>
        <button
          class="w-full my-1 py-1 bg-green-600 hover:bg-green-500 hover:shadow-lg focus:outline-none focus:shadow-outline rounded uppercase"
          @click="zoomAll"
        >Zoom all</button>
      </div>

      <!-- Map -->
      <div class="col-span-4 md:col-span-3 h-160">
        <l-map
          ref="cdmxMap"
          :zoom="zoom"
          :center="center"
          @update:zoom="zoomUpdated"
          @update:center="centerUpdated"
          @mousemove="updatePosition"
          @click="handleOnMapClicked"
        >
          <l-tile-layer :url="url"></l-tile-layer>
          <l-marker
            v-for="attraction in filteredAttractions"
            :key="attraction.properties.id"
            :lat-lng="[attraction.geometry.coordinates[1], attraction.geometry.coordinates[0]]"
          >
            <l-popup class="w-48">
              <h4 class="text-lg text-center">{{ attraction.properties.name }}</h4>
              <div class="flex justify-between items-center">
                <h4>{{ attraction.properties.category }}</h4>
                <div class="flex">
                  <svg
                    @click='editFeature(attraction)'
                    xmlns="http://www.w3.org/2000/svg"
                    viewBox="0 0 24 24"
                    class="w-4 h-4 mr-1 fill-current"
                  >
                    <path class="text-red-500" d="M4 14a1 1 0 0 1 .3-.7l11-11a1 1 0 0 1 1.4 0l3 3a1 1 0 0 1 0 1.4l-11 11a1 1 0 0 1-.7.3H5a1 1 0 0 1-1-1v-3z"/>
                    <rect width="20" height="2" x="2" y="20" class="text-red-700" rx="1"/>
                  </svg>
                  <svg
                    @click='deleteFeature(attraction.properties.id)'
                    xmlns="http://www.w3.org/2000/svg"
                    viewBox="0 0 24 24"
                    class="w-4 h-4 fill-current"
                  >
                    <path class="text-red-500" d="M5 5h14l-.89 15.12a2 2 0 0 1-2 1.88H7.9a2 2 0 0 1-2-1.88L5 5zm5 5a1 1 0 0 0-1 1v6a1 1 0 0 0 2 0v-6a1 1 0 0 0-1-1zm4 0a1 1 0 0 0-1 1v6a1 1 0 0 0 2 0v-6a1 1 0 0 0-1-1z"/>
                    <path class="text-red-700" d="M8.59 4l1.7-1.7A1 1 0 0 1 11 2h2a1 1 0 0 1 .7.3L15.42 4H19a1 1 0 0 1 0 2H5a1 1 0 1 1 0-2h3.59z"/>
                  </svg>
                </div>
              </div>
              <a :href="attraction.properties.web" blank="_traget">
                <!--
                <img v-if="$env!=='development'" :src="'img/' + attraction.properties.image" :alt="attraction.properties.name">
                -->
                <img
                  :src="'http://localhost:3000/img/' + attraction.properties.image"
                  :alt="attraction.properties.name"
                >
              </a>
            </l-popup>
          </l-marker>
        </l-map>
      </div>
    </div>

    <!-- Footer -->
    <div class="h-16 bg-gray-200 text-center">
      <h4
        class="pt-2"
      >Lattitude: {{ mousePos.lat }} | Longitude: {{ mousePos.lng }} | Zoom Level: {{ zoom }}</h4>
      <h4>
        &copy;2016
        <a href="http://millermountain.com">Miller Mounain LLC</a>
      </h4>
    </div>

    <!-- New Atttraction Dialog -->
    <new-attraction-dialog
      v-if="showAttractionDialog"
      @close-dialog="closeAttractionDialog"
      @add-feature="addFeature"
      @update-feature="updateFeature"
      :attrGeom="attrGeom"
      :attrProps="!attrProps? undefined : attrProps"
    ></new-attraction-dialog>

    <attractions-table
      v-if="showAttractionsTable"
      @close-table="closeAttractionsTable"
      :attractions="closestAttractions"
    ></attractions-table>

  </div>
</template>

<script>
// Imports
import NewAttractionDialog from '@/components/NewAttractionDialog'
import AttractionsTable from '@/components/AttractionsTable'
import mainService from '@/services/mainService'
import analyticsService from '@/services/analyticsService'
import { LMap, LTileLayer, LMarker, LPopup } from 'vue2-leaflet';

// Export component instance
export default {
  name: 'Home',

  components: {
    LMap,
    LTileLayer,
    LMarker,
    LPopup,
    NewAttractionDialog,
    AttractionsTable
  },

  async created () {
    try {
      this.attractions = (await mainService.loadAttractions()).data
    } catch (error) {
      alert (error.message)
    }
  },

  data () {
    return {
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
      zoom: 11,
      center: { lat: 19.4, lng: -99.1 },
      mousePos: { lat: 19.4, lng: -99.1 },
      attractions: {},
      closestAttractions: null,
      showAttractionDialog: false,
      showAttractionsTable: false,
      attrGeom: {},
      attrProps: null,
      filterCategory: ''
    }
  },

  computed: {
    filteredAttractions () {
      if (!this.filterCategory) {
        return this.attractions.features
      }
      return this.attractions.features
        .filter(feature => feature.properties.category === this.filterCategory)
    },
    markers () {
      if (!this.filterCategory) {
        return this.attractions.features
          .map(f => [f.geometry.coordinates[1], f.geometry.coordinates[0]])
      }
      return this.attractions.features
        .filter(feature => feature.properties.category === this.filterCategory)
        .map(f => [f.geometry.coordinates[1], f.geometry.coordinates[0]])
    }
  },

  methods: {
    zoomUpdated (zoom) {
      this.zoom = zoom;
    },
    centerUpdated (center) {
      this.center = center;
    },
    zoomAll () {
      this.$refs.cdmxMap.mapObject.fitBounds(this.markers)
    },
    zoomToLocation (coords) {
      this.$nextTick(() => {
        this.$refs.cdmxMap.mapObject.setView([ coords[1], coords[0] ], 17)
      })
    },
    updatePosition (evt) {
      this.mousePos = {
        lat: evt.latlng.lat.toFixed(5),
        lng: evt.latlng.lng.toFixed(5)
      }
    },
    handleOnMapClicked(evt) {
      if (evt.originalEvent.shiftKey) this.getClosestAttraction (evt.latlng)
      else this.setNewAttraction (evt.latlng)
    },
    setNewAttraction(latlng) {
      this.attrGeom = latlng
      this.showAttractionDialog = true
    },
    addFeature (feature) {
      this.attractions.features.push(feature)
      this.showAttractionDialog = false
      this.closeAttractionDialog ()
    },
    editFeature (feature) {
      this.attrGeom.lat = feature.geometry.coordinates[1]
      this.attrGeom.lng = feature.geometry.coordinates[0]
      this.attrProps = feature.properties
      this.showAttractionDialog = true
    },
    updateFeature (updatedFeature) {
      const i = this.attractions.features.findIndex(feature => feature.properties.id === updatedFeature.properties.id)
      this.attractions.features = [
        ...this.attractions.features.slice(0, i),
        updatedFeature,
        ...this.attractions.features.slice(i + 1)
      ]
      this.closeAttractionDialog ()
    },
    async deleteFeature (id) {
      const confirmDel = confirm ("Do you really want to delete this attraction?")
      if (confirmDel) {
        try {
          const response = (await mainService.deleteAttraction(id)).data
          if (response.success) {
            const i = this.attractions.features.findIndex(feature => feature.properties.id === id)
            this.attractions.features = [
              ...this.attractions.features.slice(0, i),
              ...this.attractions.features.slice(i + 1)
            ]
          }
        } catch (error) {
          alert(error.message)
        }
      }
    },
    async getClosestAttraction (latlng) {
      try {
        this.closestAttractions = (await analyticsService.loadClosestAttractions(`${latlng.lat}&${latlng.lng}`)).data
        if (this.closestAttractions.length) {
          this.showAttractionsTable = true
        }
      } catch (error) {
        alert(error.message)
      }
    },
    closeAttractionDialog () {
      this.showAttractionDialog = false
      this.attrGeom = {}
      this.attrProps = null
    },
    closeAttractionsTable () {
      this.showAttractionsTable = false
      this.closestAttractions = null
    }
  }
}

</script>
