import api from './api';

export default {
  loadAttractions (id) {
    if (id) {
      return api().get(`/attraction/${id}`);
    } else {
      return api().get('/attraction');
    }
  },
  loadClosestAttractions () {
    return api().get('/closest');
  },
  saveAttraction (attr) {
    return api().post('/attraction', attr);
  },
  updateAttraction (attr) {
    return api().put('/attraction', attr);
  },
  deleteAttraction (id) {
    return api().delete(`/attraction/${id}`);
  },
  uploadImage (formData) {
    return api().post('/uplImg', formData)
  }
};

